import axios from 'axios'
import qs from 'qs'
// axios.defaults.baseURL = '/apis'

// Add a response interceptor
axios.interceptors.response.use(function (response) {
  // Do something with response data
  return response;
}, function (error) {
  // Do something with response error
  error.code = error.response.status
  switch (error.response.status) {
    case 400:
      error.message = ''
      break;
    case 401:
      error.message = ''
      break;
    case 403:
      error.message = ''
      break;
    case 404:
      error.message = ','
      break;
    case 405:
      error.message = ''
      break;
    case 408:
      error.message = ''
      break;
    case 500:
      error.message = ''
      break;
    case 501:
      error.message = ''
      break;
    case 502:
      error.message = ''
      break;
    case 503:
      error.message = ''
      break;
    case 504:
      error.message = ''
      break;
    case 505:
      error.message = 'http'
      break;
    default:
      error.message = `${error.response.status}`
  }
  return Promise.reject(error);
});

async function request(options) {
  options.method = 'post'
  if (options.config === 'formData') {
    options.headers = {'Content-Type': 'multipart/form-data'}
  } else {
    options.data = qs.stringify(options.data)
  }
  return new Promise((resolve,reject) => {
    axios(options).then(res => {
      if(res.data.status === 0) {
        resolve(res.data)
      } else {
        this.$message.error(`status: ${res.data.status}, message: ${ res.data.message }`)
        reject(res.data)
      }
    }).catch(error => {
      this.$message.error(`${error.code} : ${ error.message }`)
    })
  })
}

async function requestAll (data1, data2) {
  function fun1() {
    return axios({ method: 'post', ...data1, data: qs.stringify(data1.data) })
  }
  function fun2() {
    return axios({ method: 'post', ...data2, data: qs.stringify(data2.data) })
  }
  return new Promise((resolve,reject) => {
    axios.all([fun1(), fun2()]).then(axios.spread((res1, res2) => {
      // 
      const data1 = res1.data
      const data2 = res2.data
      if(data1.status === 0 && data2.status === 0) {
        const newArr = [...data1.data, ...data2.data]
        resolve(newArr)
      } else {
        this.$message.error(`message: `)
        reject([data1,data2])
      }

    })).catch(error => {
      this.$message.error(`${error.code} : ${ error.message }`)
    })
  })
}

export default { request, requestAll }




