* Date: 2018/10/8
* Time: 11:14
*
*/
<template>
  <div>
    <PageTitle :title="''"/>
    <el-form ref="creditSettingForm" :model="creditSettingForm" label-width="120px" :rules="rules">
      <el-form-item label=":">
        <el-row>
          <el-col :span="1">1  </el-col>
          <el-col :span="3"><el-input v-model.number="creditSettingForm.get" :readonly="!isEdit"></el-input></el-col>
        </el-row>
      </el-form-item>
      <el-form-item label=":">
        <el-row>
          <el-col :span="3"><el-input v-model.number="creditSettingForm.pay" :readonly="!isEdit"></el-input></el-col>
          <el-col :span="1"> 1 </el-col>
        </el-row>
      </el-form-item>
      <el-form-item label=":">
        <el-row>
          <el-col :span="3"><el-input v-model.number="creditSettingForm.firstCheck" :readonly="!isEdit"></el-input></el-col>
          <el-col :span="1">  </el-col>
        </el-row>
      </el-form-item>
      <el-form-item label=":">
        <el-row>
          <el-col :span="3"><el-input v-model.number="creditSettingForm.everyCheck" :readonly="!isEdit"></el-input></el-col>
          <el-col :span="1">  </el-col>
        </el-row>
      </el-form-item>
      <el-form-item label=":">
        <el-row>
          <el-col :span="3"><el-input v-model.number="creditSettingForm.invite" :readonly="!isEdit"></el-input></el-col>
          <el-col :span="1">  </el-col>
        </el-row>
      </el-form-item>
      <el-form-item label=":">
        <UploadImg/>
      </el-form-item>
      <el-form-item v-if="!isEdit">
        <el-button type="primary" @click="onEdit"></el-button>
      </el-form-item>
      <el-form-item v-else>
        <el-button type="primary" @click="onSubmit('creditSettingForm')"></el-button>
        <el-button @click="onReset"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script type="text/ecmascript-6">
  import PageTitle from '@/components/PageTitle/index'
  import UploadImg from '@/components/UploadImg/index'
  export default {
    name: 'CreditSetting',
    components: { UploadImg, PageTitle },
    data() {
      return {
        isEdit: false,
        creditSettingForm: {
          get: '',
          pay: '',
          firstCheck: '',
          everyCheck: '',
          invite: '',
          img: ''
        },
        rules: {
          get: [
            { required: true, message: ''},
            { type: 'number', message: ''}
          ],
          pay: [
            { required: true, message: ''},
            { type: 'number', message: ''}
          ],
          firstCheck: [
            { required: true, message: ''},
            { type: 'number', message: ''}
          ],
          everyCheck: [
            { required: true, message: ''},
            { type: 'number', message: ''}
          ],
          invite: [
            { required: true, message: ''},
            { type: 'number', message: ''}
          ],
        }
      }
    },
    methods: {
      onSubmit(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            console.log(this.creditSettingForm)
          } else {
            return false;
          }
        });
      },
      onEdit() {
        this.isEdit = true
      },
      onReset() {
        this.isEdit = false
      }
    }
  }
</script>

<style scoped>

</style>



