<template>
  <div>
    <PageTitle :title="''"/>
    <el-table
      :data="myClassDatas"
      border
      style="width: 100%">
      <el-table-column
        prop="date"
        label=""
        width="180">
      </el-table-column>
      <el-table-column
        prop="name"
        label=""
        >
      </el-table-column>
      <el-table-column
        prop="name"
        label=""
        width="180">
      </el-table-column>
      <el-table-column
        label=""
        width="180">
        <template slot-scope="scope">
          <span> {{scope.row.name}}</span>
        </template>
      </el-table-column>
      <el-table-column
        fixed="right"
        label=""
        width="100">
        <template slot-scope="scope">
          <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="page-station">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="page"
        :page-size="10"
        layout="total, prev, pager, next, jumper"
        :total="myClassDatas.length">
      </el-pagination>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import PageTitle from '@/components/PageTitle/index'
  export default {
    name: 'myClass',
    components: { PageTitle },
    data() {
      return {
        page: 1,
        myClassDatas: [{
